import Vue from 'vue'
import Router from 'vue-router'
import AdminPageLayout from './views/AdminPageLayout.vue'

Vue.use(Router)

// login true  login page  
const alreayLogin = (to, from, next) => {
    let token = localStorage.getItem("access_token")

    if (token !== null) {
        alert("  .")
        next("/dashboard")
    } else {
        next()
    }
}

// login true 
const onlyLoginUser = (to, from, next) => {
    let token = localStorage.getItem("access_token")

    if (token === null) {
        alert("  .")
        next("/login")
    } else next()
}

// role() null  
const onlyAuthUser = (to, from, next) => {
    let token = localStorage.getItem("access_token")
    let role = localStorage.getItem("user_role")

    if (token === null) {
        alert("  .")
        next("/login")
    } else {
        if (role === "null" || role === "") {
            alert(" .")
            next(from)
        } else next()
    }
}

// role() super  
const onlySuperUser = (to, from, next) => {
    let token = localStorage.getItem("access_token")
    let role = localStorage.getItem("user_role")

    if (token === null) {
        alert("  .")
        next("/login")
    } else {
        if (role === "super") next()
        else {
            alert(" .")
            next(from)
        }
    }
}

const Login = () =>
    import ("./components/Login");
const Register = () =>
    import ("./components/Register")
const DashBoard = () =>
    import ("./components/dashboard/DashBoard");
const FaceInfo = () =>
    import ("./components/face_info/FaceInfo");
const VisitorList = () =>
    import ("./components/visitor_list/VisitorList");
const UserList = () =>
    import ("./components/user_list/UserList");
const Account = () =>
    import ("./components/Account");

export default new Router({
    routes: [{
        path: '/',
        name: 'Home',
        component: AdminPageLayout,
        children: [{
                path: '/login',
                name: 'Login',
                beforeEnter: alreayLogin,
                component: Login
            },
            {
                path: '/register',
                name: 'Register',
                component: Register
            },
            {
                path: '/dashBoard',
                name: 'DashBoard',
                beforeEnter: onlyAuthUser,
                component: DashBoard
            },
            {
                path: '/faceInfo',
                name: 'FaceInfo',
                beforeEnter: onlyAuthUser,
                component: FaceInfo
            },
            {
                path: '/visitorList',
                name: 'VisitorList',
                beforeEnter: onlyAuthUser,
                component: VisitorList
            },
            {
                path: '/userList',
                name: 'UserList',
                beforeEnter: onlySuperUser,
                component: UserList
            },
            {
                path: '/account',
                name: 'Account',
                beforeEnter: onlyLoginUser,
                component: Account
            },
        ]
    }, ]
})